      dialog: false,
      dialog2: false,
      dialogDelete: false,
      dialogDeleteHorario: false,
      validMateria: true,
      horarioIini1: "",
      horarioIini2: "",
      horario2Iini1: "",
      horario2Iini2: "",
      snackbar: false,
      colorSnack: "",
      msgSanck: "",
      sumHorario: 0,
      indexState: 0,
      nota: "NOTA: LAS HORAS TEORICAS TOTALES CONTRATADAS EN LA UNAM NO DEBERAN REBASAR DE 18 HRS/SEMANA/MES",
      headers: [
        { text: "CLAVE MATERIA", value: "claveMat", sortable: false },
        { text: "MATERIA", value: "materia", sortable: false, width: "40%" },
        { text: "TEORICAS", value: "teoricas", sortable: false },
        { text: "PRACTICAS", value: "practicas", sortable: false },
        { text: "TOTAL", value: "totalHras1", sortable: false },
        { text: "GRUPO", value: "grupo", sortable: false },
        { text: "DIAS", value: "dias", sortable: false },
        { text: "ENT", value: "ent", sortable: false },
        { text: "SAL", value: "sal", sortable: false },
        { text: "TOTAL", value: "totalHras2", sortable: false },
        { text: "ACCIONES", value: "actions", sortable: false },
      ],
      headersHorario: [
        {
          text: "",
          align: "start",
          value: "dia",
          sortable: false,
          width: "1%",
        },

        { text: "", value: "hrInicio", sortable: false, width: "1%" },
        { text: "", value: "union", sortable: false, width: "1%" },
        { text: "", value: "hrfinal", sortable: false, width: "1%" },
        { text: "", value: "actions", sortable: false, width: "1%" },
      ],
      days: [
        "Lunes",
        "Martes",
        "Miercoles",
        "Jueves",
        "Viernes",
        "Sabado",
        "Domingo",
      ],
      selectedDays: [],
      horario: [],
      horarioFinal: [],
      values: [],
      materias: [],
      nombreMaterias: [],
      editedIndex: -1,
      editedIndexHorario: -1,
      editedItemHorario: {
        dia: "",
        hrInicio: "",
        hrfinal: "",
        union: "a",
        numDia: 0,
      },
      editedItem: {
        claveMat: "",
        materia: "",
        teoricas: 0,
        practicas: 0,
        totalHras1: 0,
        grupo: "",
        dias: "",
        ent: "",
        sal: "",
        totalHras2: 0,
        numDia: 0,
        indexMateria: "",
      },
      defaultItemHorario: {
        dia: "",
        hrInicio: "",
        hrfinal: "",
        union: "a",
        numDia: 0,
      },
      defaultItem: {
        claveMat: "",
        materia: "",
        teoricas: 0,
        practicas: 0,
        totalHras1: 0,
        grupo: "",
        dias: "",
        ent: "",
        sal: "",
        totalHras2: 0,
        numDia: 0,
        indexMateria: "",
      },
      dias: [
        "LUNES",
        "MARTES",
        "MIERCOLES",
        "JUEVES",
        "VIERNES",
        "SABADO",
        "DOMINGO",
      ],
      //Style centrar texto en celdas excel
      alingCenter: {
        vertical: "middle",
        horizontal: "center",
      },
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nueva Materia" : "Editar Materia";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  mounted: function () {
    this.getMaterias();
  },
  methods: {
    sortByDay: function(items, index, isDesc) {
      items.sort((a, b) => {
          if(a.numDia < b.numDia) {
            return -1;
          }
          if(a.numDia > b.numDia) {
            return 1;
          }
          return 0;
      });
      console.log(items);
      return items;
    },
    validHorasTP() {
      if (this.editedItem.teoricas > 0 || this.editedItem.practicas > 0) {
        this.dialog2 = !this.dialog2;
      } else {
        this.snackbar = true;
        this.msgSanck = "Debe introducir horas Teoricas  Practicas";
        this.colorSnack = "red";
        this.overlay = false;
      }
    },
    assignHorario(itemHorario) {
      if (this.editedIndexHorario > -1) {
        Object.assign(this.horario[this.editedIndexHorario], itemHorario);
      } else {
        this.horario.push(itemHorario);
      }
      this.editedItemHorario = Object.assign({}, this.defaultItemHorario);
      this.editedIndexHorario = -1;
    },
    fillHorario() {
      console.log(this.selectedDays.sort());
      if (this.selectedDays.length > 0) {
        this.selectedDays.forEach((dia) => {
          if (this.horarioIini1.length > 0) {
            this.editedItemHorario.dia = this.days[dia];
            this.editedItemHorario.union = "a";
            this.editedItemHorario.hrInicio = this.horarioIini1;
            this.editedItemHorario.hrfinal = this.horarioIini2;
            this.editedItemHorario.numDia = dia;
            this.assignHorario(this.editedItemHorario);
          }
          if (this.horario2Iini1.length > 0) {
            this.editedItemHorario.dia = this.days[dia];
            this.editedItemHorario.union = "a";
            this.editedItemHorario.hrInicio = this.horario2Iini1;
            this.editedItemHorario.hrfinal = this.horario2Iini2;
            this.editedItemHorario.numDia = dia;
            this.assignHorario(this.editedItemHorario);
          }
        });

        this.sumHrsHorario();
      } else {
        this.snackbar = true;
        this.msgSanck = "Debe seleccionar por lo menos un da de la semana";
        this.colorSnack = "red";
        this.overlay = false;
        return;
      }
      this.resetHorarios();
    },
    resetHorarios() {
      this.selectedDays = [];
      this.horarioIini1 = "";
      this.horarioIini2 = "";
      this.horario2Iini1 = "";
      this.horario2Iini2 = "";
    },
    claveMatSelect(event) {
      this.editedItem.materia = this.$store.getters.getNombreMat(event);
    },
    nombreMatSelect(event) {
      this.editedItem.claveMat = this.$store.getters.getClaveMat(event);
    },
    getMaterias() {
      this.materias = this.$store.getters.getClavesMaterias;
      this.nombreMaterias = this.$store.getters.getNombreMaterias;
    },
    initialize() {
      this.values = [
        {
          claveMat: "",
          materia: "",
          teoricas: 0,
          practicas: 0,
          totalHras1: 0,
          grupo: "",
          dias: "",
          ent: "",
          sal: "",
          totalHras2: 0,
          numDia: 0,
          indexMateria: 0,
        },
      ];
    },

    editItem(item) {
      let grupo = item.indexMateria;
      console.log(this.values);
      let horasGrupo = this.values.filter(materia => materia.indexMateria == grupo);
      horasGrupo.forEach(materia => {
            //this.selectedDays.push(materia.numDia);
            this.editedItemHorario.dia = this.days[materia.numDia];
            this.editedItemHorario.union = "a";
            this.editedItemHorario.hrInicio = materia.ent;
            this.editedItemHorario.hrfinal = materia.sal;
            this.editedItemHorario.numDia = materia.numDia;
            this.assignHorario(this.editedItemHorario);
      });
      console.log("-----------");
      console.log(horasGrupo);
      console.log("-----------");
      this.editedIndex = this.values.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.values.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },
    deleteItemHorario(item) {
      this.editedIndexHorario = this.horario.indexOf(item);
      this.editedItemHorario = Object.assign({}, item);
      this.dialogDeleteHorario = true;
    },

    deleteItemConfirm() {
      let grupo = this.values[this.editedIndex].indexMateria;
      this.values.splice(this.editedIndex, 1);
      let removeGrupo = this.values.filter(materia => materia.indexMateria != grupo);
      this.values = removeGrupo;
      this.closeDelete();
    },
    deleteItemConfirmHorario() {
      this.horario.splice(this.editedIndexHorario, 1);
      this.sumHrsHorario();
      this.closeDeleteHorario();
    },

    close() {
      this.$refs.form.resetValidation();
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeHorario() {
      this.dialog2 = false;
      this.$nextTick(() => {
        this.editedItemHorario = Object.assign({}, this.defaultItemHorario);
        this.editedIndexHorario = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$refs.form.resetValidation();
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDeleteHorario() {
      this.dialogDeleteHorario = false;
      this.$nextTick(() => {
        this.editedItemHorario = Object.assign({}, this.defaultItemHorario);
        this.editedIndexHorario = -1;
      });
    },

    save() {

      if (this.$refs.form.validate()) {
        if (this.horarioFinal.length == 0) {
          this.snackbar = true;
          this.msgSanck = "Debe Registrar el Horario de la Materia";
          this.colorSnack = "red";
          this.overlay = false;
          return;
        }
        this.sumHrsTeoPrac();
        let dataTemp = this.editedItem;
        if (this.editedIndex > -1) {
          let grupo = this.values[this.editedIndex].indexMateria;
          let removeGrupo = this.values.filter(materia => materia.indexMateria != grupo);
          this.values = removeGrupo;
          this.horarioFinal.forEach((data, index) => {
            if (index == 0) {
                this.editedItem.dias = data.dia;
                this.editedItem.ent = data.hrInicio;
                this.editedItem.sal = data.hrfinal;
                this.editedItem.numDia = data.numDia;
                this.editedItem.indexMateria = dataTemp.grupo;
                this.sumHrsTeoPrac();
                this.values.push(this.editedItem);
            }else {
              this.editedItem = Object.assign({}, this.defaultItem);
              this.editedItem.dias = data.dia;
              this.editedItem.ent = data.hrInicio;
              this.editedItem.sal = data.hrfinal;
              this.editedItem.numDia = data.numDia;
              this.editedItem.indexMateria = dataTemp.grupo;
              this.sumHrsTeoPrac();
              this.values.push(this.editedItem);
            }
          });
          this.close();

          //Object.assign(this.values[this.editedIndex], this.editedItem);
        } else {
          this.horarioFinal.forEach((data, index) => {
            console.log(index);
            console.log(data);
