    saveHorario() {
      var sumhrsteoprac =
        Number(this.editedItem.teoricas) + Number(this.editedItem.practicas);
      if (this.sumHorario < sumhrsteoprac) {
        this.snackbar = true;
        this.msgSanck =
          "Las horas introducidas No cubren las " +
          sumhrsteoprac +
          " horas Teoricas y practicas capturadas ";
        this.colorSnack = "red";
        this.overlay = false;
      } else if (this.sumHorario > sumhrsteoprac) {
        this.snackbar = true;
        this.msgSanck =
          "Las horas introducidas Sobrepasan las " +
          sumhrsteoprac +
          " horas Teoricas y practicas capturadas ";
