    sumTeoPrac() {
      return (
        Number(this.editedItem.teoricas) + Number(this.editedItem.practicas)
      );
    },
    sumHrsTeoPrac() {
      var sumtotal1 =
        Number(this.editedItem.teoricas) + Number(this.editedItem.practicas);
      this.editedItem.totalHras1 = sumtotal1;
      this.editedItem.totalHras2 = this.addTimes(
        this.editedItem.ent,
        this.editedItem.sal
      );
    },
    addTimes(start, end) {
      console.log(`start=${start} end=${end}`);
      var times1 = (start + ":00").split(":");
      var times2 = (end + ":00").split(":");
      let startDate = new Date(2020, 1, 1, times1[0], times1[1], times1[2]);
      let endDate = new Date(2020, 1, 1, times2[0], times2[1], times2[2]);
      let difference = endDate.getTime() - startDate.getTime();
      if (difference < 0) {
        console.log("Error en las fechas capturadas");
      } else {
        difference = difference / 1000;
        let hourDifference = Math.floor(difference / 3600);
        difference -= hourDifference * 3600;
        let minuteDifference = Math.floor(difference / 60);
        difference -= minuteDifference * 60;
        let timeres =
          hourDifference.toString().padStart(2, "0") +
          ":" +
          minuteDifference.toString().padStart(2, "0") +
          ":" +
          difference.toString().padStart(2, "0");
        console.log(
          `${hourDifference} hours, ${minuteDifference} minutes, ${difference} seconds`
        );
        return timeres;
      }
    },
    sumField(key) {
      return this.values.reduce((a, b) => Number(a) + Number(b[key] || 0), 0);
    },
    sumHrsHorario() {
      var hourDifference = 0;
      console.log(this.horario.length);
      for (let index = 0; index < this.horario.length; index++) {
        var times1 = (this.horario[index].hrInicio + ":00").split(":");
        var times2 = (this.horario[index].hrfinal + ":00").split(":");
        let startDate = new Date(2020, 1, 1, times1[0], times1[1], times1[2]);
        let endDate = new Date(2020, 1, 1, times2[0], times2[1], times2[2]);
        let difference = endDate.getTime() - startDate.getTime();
        difference = difference / 1000;
        hourDifference = hourDifference + Math.floor(difference / 3600);
      }
      this.sumHorario = hourDifference;
    },

    sumHrs(key) {
      var suma = 0;
      for (let index = 0; index < this.values.length; index++) {
        var time = this.values[index].totalHras2;
        if (time == null || time == "" || time == 0) {
          time = "00:00:00";
        }
        suma = suma + this.timestrToSec(time);
      }
      return this.formatTime(suma);
    },
    timestrToSec(timestr) {
      var parts = timestr.split(":");
      return parts[0] * 3600 + parts[1] * 60 + +parts[2];
    },
    pad(num) {
      if (num < 10) {
        return "0" + num;
      } else {
        return "" + num;
      }
    },
    formatTime(seconds) {
      return [
        this.pad(Math.floor(seconds / 3600)),
        this.pad(Math.floor(seconds / 60) % 60),
        this.pad(seconds % 60),
      ].join(":");
    },
