      worksheet.getCell("A49").value = "NOMBRE Y FIRMA";
      worksheet.getCell("A49").alignment = this.alingCenter;

      this.setBorderThin(worksheet, ["E47", "F47", "G47","H47","I47"]);
      worksheet.mergeCells("E48:I48");
      worksheet.getCell("E48").value = "REVISO";
      worksheet.getCell("E48").alignment = this.alingCenter;
      worksheet.mergeCells("E49:I49");
      worksheet.getCell("E49").value = "NOMBRE Y FIRMA";
      worksheet.getCell("E49").alignment = this.alingCenter;

      this.setBorderThin(worksheet, ["L47", "M47", "N47","O47","P47"]);
      worksheet.mergeCells("L48:P48");
      worksheet.getCell("L48").value = "CAPTURO";
      worksheet.getCell("L48").alignment = this.alingCenter;
      worksheet.mergeCells("L49:P49");
      worksheet.getCell("L49").value = "NOMBRE Y FIRMA";
      worksheet.getCell("L49").alignment = this.alingCenter;
            this.setBorderThick(worksheet, [
        "A50",
        "B50",        
        "C50",
        "D50",
        "E50",
        "F50",
        "G50",
        "H50",
        "I50",
        "J50",
        "K50",
        "L50",
        "M50",
        "N50",
        "O50",
        "P50",]);
        this.setBorderThickleft(worksheet, ["A46","A47","A48","A49"]);
        this.setBorderThickright(worksheet, ["P46","P47","L48","L49"]);
        this.setBorderThickCustom(worksheet, ["A45"]);
        this.setBorderThickCustom3(worksheet, ["A50"]);
        this.setBorderThickCustom2(worksheet, ["P50"]);

      fileName = fileName + ".xlsx";
      workbook.xlsx.writeBuffer().then((data) => {
        let blob = new Blob([data], {
          type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
        });
        saveAs(blob, fileName);
      });
    },
    calculateColumnWidth(tHeader) {
      let colWidth = [];
      for (let i = 0; i < tHeader.length; i++) {
        if (colWidth[i] && colWidth[i] < tHeader[i].length) {
          colWidth[i] = tHeader[i].length;
        } else if (colWidth[i] == undefined) {
          colWidth[i] = tHeader[i].length;
        }
      }
      console.log(colWidth);
      return colWidth;
    },
  },
};
</script>
