                    <td style="display: none">{{ item.totHoras }}</td>
                  </tr>
                </template>
              </v-data-table>
            </div>

            <!-- <div style="left:10px;margin-top:0px;background:#DFA924;width:100%;height:30px">Alfabetico</div> -->
            <!-- Alfabetico -->
            <div
              style="
                padding-top: 25px;
