<script>
import Expediente from "../components/Expediente.vue";
import Materias from "../components/MateriasDos.vue";
import Personales from "../components/PersonalesNuevaSol.vue";
import Seguimiento from "../components/Seguimiento.vue";
import ModalNuevaSol from "../components/ModalNuevaSol.vue";

export default {
  name: "nuevasolicitud",
  components: {
    Personales,
    Seguimiento,
    Expediente,
    Materias,
    ModalNuevaSol,
  },
  props: ["dialogparam"],
  data() {
    return {
      currentStep: 1,
      lastStep: 2,
      validStep: false,
      stepForm: [],
      dialog: false,
      selected: [{ rfc: "" }],
      tab: "",
      itemsPerPage: 4,
      overlay: false,
      loading: false,
      snackbar: false,
      colorSnack: "",
      msgSanck: "",
      text: "",
      fav: true,
      menu: false,
      message: false,
      hints: true,
      profResult: [],
      alfResult: [],
      svfResult: [],

      steps: [
        {
          name: "Datos personales",
          desc: "Captura de datos Personales",
          rules: [(v) => !!v || "Campo requerido."],
          valid: true,
        },
        {
          name: "Alta de materias",
          desc: "Captura de Materias",
          rules: [(v) => !!v || "Campo requerido."],
          valid: true,
        },
      ],
      alfabeticoHeaders: [
        { text: "Clave.", value: "cveCategoria" },
        { text: "Categoria", value: "categoria" },
        { text: "Tipo emp", value: "tipoemp" },
        { text: "Programatico", value: "codProgramatico" },
      ],
      svigenteHeaders: [
        { text: "Clave.", value: "categoria" },
        { text: "Catecoria", value: "descCategoria" },
        { text: "Area asig", value: "areaAsignatura" },
        { text: "F Ini", value: "fechaIni" },
        { text: "F Termino", value: "fechaTer" },
        { text: "Sueldo", value: "sueldo" },
      ],
    };
  },
  computed: {
    console: () => console,
    window: () => window,
  },
  mounted: function () {
    //sustituye la linea de abajo // this.$store.state.solicitudes.selectedSolicitud = {};
    this.$store.dispatch("resetResultadoSeleccionadoState");
    this.$store.dispatch("resetResultadoBusquedaState");
    this.dialog = this.dialogparam;
  },
  methods: {
    checkValues(values) {
      console.log(values);
      this.$store.dispatch("updateRslSelect", values);
      this.dialog = values.dialog;
    },
    stepComplete(step) {
      return this.currentStep > step;
    },
    stepStatus(step) {
      return this.currentStep > step ? "green" : "blue";
    },
    validateStep(n) {
      this.steps[n].valid = false;
      let v = this.$refs.stepForm[n].validate();
      if (v) {
        this.steps[n].valid = true;
        // continue to next
        this.currentStep = n + 2;
      }
    },
    writting() {
      this.$store.state.resultadosBusqueda != ""
        ? (this.menu = true)
        : (this.menu = false);
      this.busqueda();
    },
    busqueda() {
      //carina
      this.profResult = this.$store.state.database.dbPROF.data.filter(
        (data) =>
          data.curp
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.noEmpleado
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.nombreCompleto
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.rfc
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase())
      );
      console.log("Coincidencias");
      console.log(this.profResult);

      //---------------------------------------------------------------
      this.$store.state.resultadosBusqueda = [];

      if (this.profResult.length > 0) {
        this.profResult.forEach((element) => {
          this.$store.state.resultadosBusqueda.push({
            folioDGP: "F00000001",
            noEmpleado: element.noEmpleado,
            nombreCompleto: element.nombreCompleto,
            rfc: element.rfc,
            curp: element.curp,
            email: element.email,
            tipoContratacion: element.tipoContratacion,
            domicilio: element.domicilio,
            antiguedad: element.antiguedad,
            edoCivil: element.edoCivil,
            genero: element.genero,
            nacionalidad: element.nacionalidad,
            nombramientoEneo: element.nombramientoEneo,
            telCasa: element.telCasa,
            telCelular: element.telCelular,
            telTrabajo: element.telTrabajo,
          });
        });
      }

      console.log(this.$store.state.resultadosBusqueda);
    },
    toggle(item, isSelected, select, e) {
      select(!isSelected);

      console.log("clicking row");
      this.alfResult = [];
      this.alfResult = this.$store.state.database.dbALF.data.filter((data) =>
        data.noEmpleado.toUpperCase().includes(item.noEmpleado)
      );
      console.log("=============================================");
      console.log("Alfabetico:");
      console.log(this.alfResult);

      this.svfResult = [];
      this.svfResult = this.$store.state.database.dbSV.data.filter((data) =>
        data.noEmpleado.toUpperCase().includes(item.noEmpleado)
      );
      console.log("=============================================");
      console.log("Situacion vigente:");
      console.log(this.svfResult);

      this.$store.state.resultadoSeleccionado = item;
      console.log("seleccionado = %o", item);
    },
  },
};
</script>

