                            background: #dfa924;
                          "
                        >
                          <v-data-table
                            dense
                            :items-per-page="5"
                            height="150"
                            style=""
                            item-key="noEmpleado"
                            hide-default-footer
                            :headers="alfabeticoHeaders"
                            :items="alfResult"
                          >
                            <template v-slot:item="{ item }">
                              <tr style="width: 100%">
                                <td>{{ item.categoria }}</td>
                                <td>{{ item.descCategoria }}</td>
                                <td>{{ item.tipoemp }}</td>
                                <td>{{ item.codProgramatico }}</td>
                              </tr>
                            </template>
                          </v-data-table>
                        </div>

                        <!-- situacion vigente -->
                        <div
                          v-if="existe"
                          style="
                            padding-top: 25px;
                            padding-left: 10px;
                            padding-right: 10px;
                            background: #dfa924;
                          "
                        >
                          <v-data-table
                            dense
                            :items-per-page="5"
                            height="150"
                            style=""
                            item-key="noEmpleado"
                            hide-default-footer
                            :headers="svigenteHeaders"
                            :items="svfResult"
                          >
                            <template v-slot:item="{ item }">
                              <tr style="width: 100%">
                                <td>{{ item.categoria }}</td>
                                <td>{{ item.descCategoria }}</td>
                                <td>{{ item.tipoemp }}</td>
                                <td>{{ item.fechaIni }}</td>
                                <td>{{ item.fechaTer }}</td>
                                <td>{{ item.sueldo }}</td>
                              </tr>
                            </template>
                          </v-data-table>
                        </div>

                        <v-card-actions style="background: #dfa924">
                          <v-spacer></v-spacer>

                          <v-btn
                            v-if="!existe"
                            color="#193051"
                            text
                            @click="openModal2()"
                          >
                            Registro Nuevo
                          </v-btn>
                          <v-btn
                            v-if="existe"
