                            text
                            @click="openModal2Exist()"
                          >
                            Seleccionar Profesor
                          </v-btn>
                          <v-btn color="#193051" text @click="menu = false">
                            Cerrar
                          </v-btn>
                        </v-card-actions>
                      </v-card>
                    </v-menu>
                  </div>
                </v-card>
              </v-col>
            </v-row>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="$router.push({ name: 'home' })"
              >Cancelar</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
    <v-dialog persistent v-model="dialog2" width="500">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-card>
          <v-toolbar color="#193051" class="text-h5" dark style="color: white">
            <div class="font-weight-black">
              Nueva solicitud de movimiento acadmico
            </div>
          </v-toolbar>
          <v-card-text>
            <v-row no-gutters justify="center" align="center">
              <v-col cols="12" lg="9">
                <v-select
                  dense
                  v-model="tipoMovimiento"
                  align="center"
                  :items="tipoMovimientos"
                  label="Tipo de Movimiento"
                  outlined
                  :rules="[(v) => !!v || 'Campo requerido']"
                  required
                ></v-select>
              </v-col>
              <v-col cols="12" lg="9">
                <v-select
                  dense
                  align="center"
                  v-model="tipoContra"
                  :items="tipoContrato"
                  label="Tipo de Contrato"
                  outlined
                  :rules="[(v) => !!v || 'Campo requerido']"
                  required
                ></v-select>
              </v-col>
              <v-col cols="6" lg="6">
                <v-menu
                  dense
                  ref="menu1"
                  v-model="menu1"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="dateFormatted"
                      label="Fecha Inicio"
                      hint="fecha en formato DD/MM/YYYY "
                      persistent-hint
                      prepend-icon="mdi-calendar"
                      v-bind="attrs"
                      @blur="date = parseDate(dateFormatted)"
                      v-on="on"
                      :rules="[(v) => !!v || 'Campo requerido']"
                      required
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="date"
                    no-title
                    @input="menu1 = false"
                    locale="es-mx"
                  ></v-date-picker>
                </v-menu> </v-col
              ><v-col cols="6" lg="6">
                <v-menu
                  dense
                  ref="menu2"
                  v-model="menu2"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-model="dateFormattedFin"
                      label="Fecha Termino"
                      hint="fecha en formato DD/MM/YYYY "
                      persistent-hint
                      prepend-icon="mdi-calendar"
                      v-bind="attrs"
                      @blur="dateFin = parseDate(dateFormattedFin)"
                      v-on="on"
                      :rules="[(v) => !!v || 'Campo requerido']"
                      required
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="dateFin"
                    no-title
                    locale="es-mx"
                    @input="menu2 = false"
                  ></v-date-picker>
                </v-menu>
              </v-col>
            </v-row>
          </v-card-text>
