          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click="validate" :disabled="!valid">
              Aceptar</v-btn
            >

            <v-btn color="primary" @click="dialog2 = false">Cancelar</v-btn>
          </v-card-actions>
        </v-card>
      </v-form>
    </v-dialog>
