</template>
<script>
export default {
  name: "modalNuevaSolicitud",
  props: {
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      dialog2: false,
      existe: false,
      valid: true,
      menu1: false,
      menu2: false,
      dateFormatted: null,
      dateFormattedFin: null,
      date: null,
      dateFin: null,
      tipoMovimiento: "",
      tipoContra: "",
      menu: false,
      itemsPerPage: 4,
      valoresSeleccionados: {},
      profResult: [],
      alfResult: [],
      svfResult: [],
      tipoMovimientos: this.tipmov(),
      tipoContrato: ["Interino", "Definitivo"],
      alfabeticoHeaders: [
        { text: "Clave.", value: "cveCategoria" },
        { text: "Categoria", value: "categoria" },
        { text: "Tipo emp", value: "tipoemp" },
        { text: "Programatico", value: "codProgramatico" },
      ],
      svigenteHeaders: [
        { text: "Clave.", value: "categoria" },
        { text: "Catecoria", value: "descCategoria" },
        { text: "Area asig", value: "areaAsignatura" },
        { text: "F Ini", value: "fechaIni" },
        { text: "F Termino", value: "fechaTer" },
        { text: "Sueldo", value: "sueldo" },
      ],
    };
  },

  computed: {
    cerrar() {
      this.menu = false;
      //this.$router.push({ name: "home" });
      // $router.push({ name: "home" });
    },
    computedDateFormatted() {
      return this.formatDate(this.date);
    },
    computedDateFormattedFin() {
      return this.formatDate(this.dateFin);
    },
  },
  watch: {
    date(val) {
      this.dateFormatted = this.formatDate(this.date);
    },
    dateFin(val) {
      this.dateFormattedFin = this.formatDate(this.dateFin);
    },
    existe(val) {
      this.tipoMovimientos = this.tipmov(this.existe);
    },
  },
  mounted: function () {
    // this.$store.dispatch("resetResultadoSeleccionadoState");
    this.$store.dispatch("resetResultadoBusquedaState");
  },
  methods: {
    tipmov(existe) {
      return existe
        ? ["Contratacin por Reingreso"]
        : ["Contratacin Nuevo Ingreso"];
    },
    openModal2() {
      this.menu = false;
      this.dialog2 = true;
    },
    openModal2Exist() {
      this.menu = false;
      this.dialog2 = true;
      this.existe = true;
    },
    writting() {
      this.$store.state.resultadosBusqueda != ""
        ? (this.menu = true)
        : (this.menu = false);
      this.busqueda();
    },
    busqueda() {
      //carina
      this.profResult = this.$store.state.database.dbPROF.data.filter(
        (data) =>
          data.curp
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.noEmpleado
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.nombreCompleto
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase()) ||
          data.rfc
            .toUpperCase()
            .includes(this.$store.state.resultadoSearch.toUpperCase())
      );
      console.log("Coincidencias");
      console.log(this.profResult);

      //---------------------------------------------------------------
      this.$store.state.resultadosBusqueda = [];

      if (this.profResult.length > 0) {
        this.profResult.forEach((element) => {
          this.$store.state.resultadosBusqueda.push({
            folioDGP: "F00000001",
            noEmpleado: element.noEmpleado,
            nombreCompleto: element.nombreCompleto,
            rfc: element.rfc,
            curp: element.curp,
            email: element.email,
            tipoContratacion: element.tipoContratacion,
            domicilio: element.domicilio,
            antiguedad: element.antiguedad,
            edoCivil: element.edoCivil,
            genero: element.genero,
            nacionalidad: element.nacionalidad,
            nombramientoEneo: element.nombramientoEneo,
            telCasa: element.telCasa,
            telCelular: element.telCelular,
            telTrabajo: element.telTrabajo,
          });
        });
      } else {
        this.existe = false;
        this.menu = true;
      }

      console.log(this.$store.state.resultadosBusqueda);
    },
    toggle(item, isSelected, select, e) {
      select(!isSelected);
      this.existe = true;
      console.log("clicking row");
      this.alfResult = [];
      this.alfResult = this.$store.state.database.dbALF.data.filter((data) =>
        data.noEmpleado.toUpperCase().includes(item.noEmpleado)
      );
      console.log("=============================================");
      console.log("Alfabetico:");
      console.log(this.alfResult);

      this.svfResult = [];
      this.svfResult = this.$store.state.database.dbSV.data.filter((data) =>
        data.noEmpleado.toUpperCase().includes(item.noEmpleado)
      );
      console.log("=============================================");
      console.log("Situacion vigente:");
      console.log(this.svfResult);

      this.$store.state.resultadoSeleccionado = item;
      console.log("seleccionado = %o", item);
    },
    valoresForm() {
      return {
        tipoMovimiento: this.tipoMovimiento,
        tipoContra: this.tipoContra,
        fechaInicio: this.date,
        fechaFin: this.dateFin,
        dialog: false,
      };
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.menu = false;
        this.valoresSeleccionados = this.valoresForm();
        this.$emit("valoresSelec", this.valoresSeleccionados);
        this.dialog2 = false;
      }
    },
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    parseDate(date) {
      if (!date) return null;

      const [month, day, year] = date.split("/");
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
  },
};
</script>
<style lang="css" scope>
